.social-share{margin:.5em 0;padding-top:.7em;padding-bottom:.6em;font-size:28px;text-align:center;background:#fcfcfc}.social-share a{cursor:pointer;text-decoration:none}.social-share a i{margin:0 10px;font-style:normal;color:#BCC1C4;transition:color .15s linear;outline:none}.social-share a i:hover{color:#309E85;transform:rotate(360deg);transition:all .8s ease-in-out}.social-share a.weixin-qr .qr-code{display:none}.social-share a.weixin-qr:hover .qr-code{margin-top:20px;display:block}


